
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { getManagerInfo, addManager, getFunction, updateManagerInfo, deleteManager } from '@/api/'
export default {
components: {
GroupTitle,
Group,
Cell,
XInput,
XSwitch,
Checklist,
XButton
},
data () {
return {
powerCheck: [],
powerList: [],
superPowerCheck: [],
superPowerList: [],
powerOn: false,
loading: false,
loading2: false,
count: 3,
info: {},
is_merchant: 0,
curUserIsSuper: false,
hasSuperManager: false,
curIsMerchant: 0
}
},
beforeRouteEnter (to, from, next) {
document.title = '管理员设置'
next()
},
created () {
var info = JSON.parse(localStorage.getItem('managerInfo'))
this.info = info
getFunction({ht_id: this.$route.query.id, mc_id: this.$route.query.mc_id}).then((res) => {
if (Array.isArray(res.result.data)) {
res.result.data.forEach((v) => {
v.key = v.id
v.value = v.name
if (!this.$route.query.type) {
if (v.path !== '/Manager') {
this.powerCheck.push(v.id.toString())
}
}
if (v.path !== '/Manager') {
this.powerList.push(v)
} else {
v.inlineDesc = '只能设置一个超级管理员有此权限'
this.superPowerList.push(v)
}
})
console.log(res.result.flag)
this.hasSuperManager = res.result.flag
this.curUserIsSuper = res.result.is_super
}
})
if (this.$route.query.type) {
getManagerInfo({ht_id: this.$route.query.id, mc_id: this.$route.query.mc_id}).then((res) => {
if (res.result) {
this.is_merchant = ~~(res.result.info.is_merchant)
var powerChecks = []
res.result.function.map((v) => {
if (v.path !== '/Manager') {
powerChecks.push(v.id.toString())
} else {
this.superPowerCheck.push(v.id.toString())
}
})

this.$set(this.$data, 'powerCheck', powerChecks)
this.powerOn = Boolean(~~(res.result.info.is_allow_in))
this.count = ~~(res.result.info.daily_screen_count)
this.curIsMerchant = parseInt(res.result.is_merchant)
}
})
}
},
mounted () {
},
methods: {
countValid (value) {
var toV = parseInt(value)
if (!Number.isNaN(toV)) {
this.count = toV > 50 ? 50 : toV
}
},
handleManager () {
this.loading2 = true
var params = {
ht_id: this.$route.query.id,
mc_id: this.$route.query.mc_id,
allow: this.powerOn ? 1 : 0,
time: 60,
num: this.count,
function_id: this.powerOn ? this.powerCheck.concat(this.superPowerCheck).join(',') : ''
}
var _action = addManager
var _text = '添加成功'
if (this.$route.query.type) {
_action = updateManagerInfo
_text = '更新成功'
}
_action(params).then((res) => {
this.$vux.toast.show({
text: _text,
isShowMask: false
})
if (this.curUserIsSuper && this.curIsMerchant === 0 && params.function_id.indexOf(this.superPowerList[0].id) === -1) {

this.$router.go(-2)
} else {
this.$router.go(-1)
}
}).finally(() => {
this.loading2 = false
})
},
deleteManager () {
var _this = this
this.$vux.confirm.show({
title: '提示',
content: '确定删除该管理员？',
onCancel () {
},
onConfirm () {
_this.loading = true
deleteManager({ht_id: _this.$route.query.id, mc_id: _this.$route.query.mc_id}).then((res) => {
_this.$vux.toast.show({
text: '删除成功',
isShowMask: false
})
_this.$router.go(-1)
}).finally(() => {
_this.loading = false
})
}
})
},
powerOnChange (currentValue) {
this.powerOn = currentValue
}
}
}



